#comentarios.page-content {
    background-color: #0b0b0a;
    padding: 100px 0 100px 0;
  }
  #comentarios.page-content h2.text-center {
    color: #fff;
  }
  #comentarios .card.mb-4 .card-head {
    padding-top: 20px;
    padding-left: 0;
    color: #818182;
    text-align: left;
  }
  #comentarios .card.mb-4 .card-head .box-line {
    display: inline-block;
    padding: 7px 0;
    background-color: #ffc107;
  }
  #comentarios .card.mb-4 .card-head .box-title {
    display: inline-block;
    padding: 7px 0;
    text-transform: uppercase;
  }
  #comentarios .card.mb-4 .card-head .box-title p {
    padding-left: 20px;
  }
  #comentarios .card.mb-4 .card-body {
    padding-top: 0;
    padding-bottom: 0;
  }
  #comentarios .card.mb-4 .card-body p {
    padding-left: 10px;
    text-align: justify;
    color: #818182;
  }
  #comentarios .card.mb-4 .card-footer {
    display: inherit;
    background-color: #fff;
    border: none;
    padding-left: 30px;
    padding-bottom: 30px;
    color: #818182;
  }
  #comentarios .card.mb-4 .card-footer .box-title {
    padding-left: 10px;
    margin-top: 5px;
    margin-bottom: 0;
  }
  #comentarios .card.mb-4 .card-footer .box-title .name {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 0;
    text-transform: uppercase;
  }
  #comentarios .card.mb-4 .card-footer .box-title .position {
    font-size: 12px;
    text-transform: uppercase;
  }