#parallax .container-fluid {
  padding: 100px 200px 107px;
}
#parallax .container-fluid p {
  color: grey;
}
#parallax .container-fluid h2 {
  color: #fff;
}
.list-progress__item {
  display: inline-flex;
  padding-top: 20px;
  width: 400px;
  height: 122px;
  font-size: 4rem;
  color: #ffc107;
  background-repeat: no-repeat;
}
.list-progress__item i {
  padding-top: 10px;
  padding-left: 50px;
}
.list-progress__item p {
  font-size: 0.8rem;
  text-transform: uppercase;
  color: grey;
}
.list-progress__item p strong {
  font-size: 1.5rem;
  color: #fff;
}
.list-progress__item.left {
  transform: skewX(-14deg);
  background-color: hsla(0, 0%, 54.5%, 0.2);
}
.list-progress__item.left i,
.list-progress__item.left p {
  transform: skewX(-167deg);
}
.list-progress__item.center {
  height: 0;
  border-right: 30px solid transparent;
  border-left: 30px solid transparent;
  padding-top: 0;
  width: 400px;
  border-bottom: 122px solid hsla(0, 0%, 54.5%, 0.2);
}
.list-progress__item.center .list-progress__inner,
.list-progress__item.center i {
  padding-top: 30px;
}
.list-progress__item.right {
  transform: skewX(14deg);
  background-color: hsla(0, 0%, 54.5%, 0.2);
}
.list-progress__item.right i,
.list-progress__item.right p {
  transform: skewX(167deg);
}
.list-progress__inner {
  display: grid;
  padding-top: 10px;
  padding-left: 20px;
}
