body {
    background-color: #f2f2f2;
    overflow-x: hidden;
    color: #818182;
  }
  body h1,
  body h2,
  body h3,
  body h4,
  body h5 {
    font-family: Raleway, sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 20px;
    color: #0b0b0a;
  }
  .section-title .separator {
    position: relative;
    margin-top: 10px;
    display: inline-block;
  }
  .section-title .separator:before {
    content: "";
    display: block;
    margin: auto;
    left: 50%;
    height: 1px;
    width: 100px;
    bottom: -15px;
    background: #ffc107;
    transition: width 0.8s ease, background-color 0.8s ease;
    -webkit-transition: width 0.8s ease, background-color 0.8s ease;
    -o-transition: width 0.8s ease, background-color 0.8s ease;
    -ms-transition: width 0.8s ease, background-color 0.8s ease;
    -moz-transition: width 0.8s ease, background-color 0.8s ease;
    margin-bottom: 6px;
  }
  .section-title .separator:after {
    width: 50px;
    display: block;
    margin: auto;
    content: "";
    height: 1px;
    background: #ffc107;
    bottom: 0;
    left: 50%;
    transition: width 0.8s ease, background-color 0.8s ease;
    -webkit-transition: width 0.8s ease, background-color 0.8s ease;
    -o-transition: width 0.8s ease, background-color 0.8s ease;
    -ms-transition: width 0.8s ease, background-color 0.8s ease;
    -moz-transition: width 0.8s ease, background-color 0.8s ease;
  }
