#caracteristicas {
    padding: 100px 0;
  }
  #caracteristicas .container .row .text-left,
  #caracteristicas .container .row .text-right {
    padding-top: 30px;
  }
  #caracteristicas .container .row .text-center {
    padding-top: 0;
  }
  #caracteristicas img.img-fluid {
    width: 80%;
    padding-top: 20px;
  }
  #caracteristicas img.img-fluid:hover {
    padding-top: 0;
  }
  @media screen and (max-width: 1199px) and (min-width: 992px) {
    #caracteristicas img.img-fluid {
      width: 90%;
      padding-top: 80px;
    }
    #caracteristicas img.img-fluid:hover {
      padding-top: 60px;
    }
  }
  @media screen and (max-width: 991px) {
    #caracteristicas .text-left,
    #caracteristicas .text-right {
      text-align: center !important;
    }
  }