section#galeria {
    padding: 100px 200px;
  }
  section#galeria p.text-center {
    color: grey;
  }
  .owl-item {
    width: 178px;
    margin-right: 20px;
  }
  .owl-item.active {
    width: 300px;
    height: 134px;
  }
  @media screen and (max-width: 1572px) and (min-width: 1200px) {
    section#galeria {
      padding: 100px;
    }
  }
  @media screen and (max-width: 1199px) {
    section#galeria {
      padding: 100px 80px;
    }
  }
  