#appcar {
    background-color: #0b0b0a;
    padding: 0;
  }
  #appcar .card.car {
    -webkit-transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    background-color: #fff;
    position: relative;
    overflow: hidden;
    padding: 0;
    margin: 20px;
    border: none;
    border-radius: 0;
    margin: 0;
  }
  #appcar .card.car .card__info-date {
    position: absolute;
    margin-left: 0;
    margin-top: 0;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.8);
    height: 500px;
    top: 0;
    z-index: 2;
    padding-top: 30px;
  }
  #appcar .card.car .card__info-date .card-head {
    padding-top: 20px;
    padding-left: 0;
    color: #818182;
    text-align: left;
  }
  #appcar .card.car .card__info-date .card-head .box-line {
    display: inline-block;
    background-color: #ffc107;
  }
  #appcar .card.car .card__info-date .card-head .box-title {
    display: inline-block;
    text-transform: uppercase;
  }
  #appcar .card.car .card__info-date .card-head .box-title h5 {
    color: #fff;
    padding-left: 15px;
  }
  #appcar .card.car .card__info-date .card-body p {
    padding-left: 0;
    padding-right: 60px;
  }
  @media screen and (max-width: 976px) and (min-width: 576px) {
    .card-body {
      padding-top: 0;
    }
    .card-body p {
      padding: 0 !important;
    }
  }
  