section#principal {
    min-height: 600px;
    padding-top: 0;
  }
  section#principal img {
    margin: 0 auto;
  }
  section#principal .img-fluid {
    max-width: 100%;
    height: 600px;
  }
  #principal .carousel-inner .container .title.text-left {
    padding-bottom: 5px;
  }
  #principal .carousel-inner .container .title.text-left .box-line.text-left {
    display: inline-block;
  }
  #principal
    .carousel-inner
    .container
    .title.text-left
    .box-description.text-left {
    display: inline-block;
  }
  #principal .carousel-inner .container .d-none.d-lg-block.text-left {
    padding-bottom: 170px;
  }
  #principal .carousel-inner .container .box-line {
    display: inline-block;
    padding: 7px 0;
    background-color: #ffc107;
    -webkit-transform: skewX(16deg);
    transform: skewX(16deg);
  }
  #principal .carousel-inner .container .box-description {
    display: inline-block;
    padding: 7px 0;
    background-color: grey;
    -webkit-transform: skewX(16deg);
    transform: skewX(16deg);
  }
  #principal .carousel-inner .container .box-description .text {
    -webkit-transform: skewX(160deg);
    transform: skewX(160deg);
  }
  #principal .carousel-inner .container .title h1 {
    color: #fff;
  }
  @media screen and (max-width: 991px) {
    .wow.fadeInLeft.carousel-caption.slider {
      top: 180px;
    }
  }